.Login form {
    max-width: 70%;
    margin: 0 auto;
    padding: 10% 0;
}

.Login .title {
    color: white;
    margin-bottom: 1em;
}

/* --- Form inputs */

/* Style the input container */
.Login .input-container {
    display: flex;
    width: 100%;
    margin-bottom: 15px;
}

/* Style the form icons */
.Login .icon {
    padding: 10px;
    background: dodgerblue;
    color: white;
    min-width: 50px;
    text-align: center;
}

/* Style the input fields */
.Login .input-field {
    width: 100%;
    padding: 10px;
    outline: none;
    border-color: transparent;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.Login .input-field:focus {
    border: 2px solid dodgerblue;
}

/* --- Form buttons */

/* Set a style for the submit button */
.Login .btn {
    /* background-color: dodgerblue; */
    /* color: white; */
    padding: 15px 20px;
    border: none;
    cursor: pointer;
    opacity: 0.9;
    width: 100%;
    /* font-size: larger; */
}

.Login .btn:hover {
    opacity: 1;
}

.Login .i-username {
    background-image: url("../../assets/username_jedi.png");
    background-size: 80% 80%;
    background-position: center center;
    background-repeat: no-repeat;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.Login .i-password {
    background-image: url("../../assets/password_key.svg");
    background-size: 70% 70%;
    background-position: center center;
    background-repeat: no-repeat;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

